body {
  font-family: "Roboto Condensed", sans-serif;
  font-family: "Space Grotesk", sans-serif;
  // Color Profile
  --text-color: #1b1b1b;
  --col-primary: #f2f2f2;
  --col-magenta: #ff00ff;
  --filter-magenta: brightness(0) saturate(100%)
  invert(17%) sepia(93%) saturate(3640%)
  hue-rotate(294deg) brightness(116%) contrast(126%);

  // Fonts
  --robo-con: "Roboto Condensed", sans-serif;
  --space: "Space Grotesk", sans-serif;

  // CSS Misc
  --spacing: 100px;
  --border-radius: 10px;

  //Components
  .text-color { color: --text-color; }
  .bg-primary { background-color: --col-primary; }
  .bg-secondary { background-color: --col-magenta; }
  .font-robo { font-family: --robo-con; }
  .font-space { font-family: --space; }
  .p-default { padding: --spacing; }
  .p-top-default { padding-top: --spacing; }
  .p-bottom-default { padding-bottom: --spacing; }
  .p-half { padding: --spacing / 2; }
  .p-top-half { padding-top: --spacing / 2; }
  .p-bottom-half { padding-bottom: --spacing / 2; }
  .m-default { margin: --spacing; }
  .m-top-default { margin-top: --spacing; }
  .m-bottom-default { margin-bottom: --spacing; }
  .m-half { margin: --spacing / 2; }
  .m-top-half { margin-top: --spacing / 2; }
  .m-bottom-half { margin-bottom: --spacing / 2; }
}

// Content Styles
body {
  margin: 0;
  width: 100%;

  p {
    font-family: "Roboto Condensed", sans-serif;
  }

  h1,
  h2,
  h3,
  h4 {
    font-family: "Space Grotesk", sans-serif;
  }
}

.full-page {
  overflow-x: clip;

  // Page Load
  &.loaded {
    .side-menu {
      width: 25%;
      &.left {
        left: 0;
      }
      .nLeft {
        

        a {
          left: 0%;
        }
      }
    }
  }

  // Page Scroll
  &.scroll {
    .side-menu {
      width: 15%;
      &.right {
        right: 0;
        transition: 1s ease 0.1s;
      }

      .nLeft .menu a {
        padding: 10px 0 10px 25px;
      }
    }
  }
}

// Side Menu
.side-menu {
  position: fixed;
  top: 0;
  width: 25%;
  height: 100dvh;
  z-index: 1000;
  transition: all 1s ease 0.1s;

  &.left {
    left: -35%;
    transition: all 1s ease 0.1s, left 1s ease 0.5s;
  }

  &.right {
    width: 0%;
    right: -35%;
  }

  .img-wrapper {
    position: absolute;
    width: 100%;
    height: 100%;
     img {
      width: 100%;
      height: 100%;
      object-fit: cover;
     }
  }

  .nLeft {
    position: relative;
    height: 100%;
    width: 100%;
    clip-path: polygon(0% 0%, 100% 0%, calc(100% - 100px) 100%, 0% 100%);

    .menu {
      position: relative;
      top: 50%;
      transform: translateY(-50%);
      left: 0;
      display: flex;
      flex-direction: column;

      a {
        position: relative;
        left: -100%;
        padding: 10px 0 10px 75px;
        margin: 5px 0;
        background-color: hsla(0, 0%, 100%, 0.1);
        font-family: "Space Grotesk", sans-serif;
        color: var(--col-primary);

        &:nth-child(1) {
          transition: all 0.35s ease, left 0.5s ease 1.5s, padding 1s ease;
        }

        &:nth-child(2) {
          transition: all 0.35s ease, left 0.5s ease 1.75s, padding 1s ease;
        }

        &:nth-child(3) {
          transition: all 0.35s ease, left 0.5s ease 2s, padding 1s ease;
        }

        &:nth-child(4) {
          transition: all 0.35s ease, left 0.5s ease 2.25s, padding 1s ease;
        }

        &:hover,
        &:focus-visible {
          color: var(--col-magenta);
          text-decoration: none;
          background-color: hsla(0, 0%, 100%, 0);
        }

        i {
          margin-right: 15px;
        }
      }
    }
  }

  .nRight {
    position: absolute;
    top: 0;
    right: 0;
    width: 100%;
    height: 100%;
    clip-path: polygon(100% 0%, calc(0% + 100px) 0%, 0% 100%, 100% 100%);

    .details {
      position: relative;
      bottom: 25px;
      right: 25px;
      height: 100%;
      display: flex;
      flex-direction: column;
      justify-content: end;
      

      .socials {
        flex-direction: column;
        align-items: end;

        a {
          position: relative;
          right: -150%;
        }
        a:nth-child(1) {
          transition: 0.5s ease 0s;
        }
        a:nth-child(2) {
          transition: 0.5s ease 0.05s;
        }
        a:nth-child(3) {
          transition: 0.5s ease 0.1s;
        }
        a:nth-child(4) {
          transition: 0.5s ease 0.15s;
        }
        a:nth-child(5) {
          transition: 0.5s ease 0.2s;
        }

        &.active {
          a {
            right: 0;
          }
          a:nth-child(1) {
            transition: 0.5s ease 0.2s;
          }
          a:nth-child(2) {
            transition: 0.5s ease 0.15s;
          }
          a:nth-child(3) {
            transition: 0.5s ease 0.1s;
          }
          a:nth-child(4) {
            transition: 0.5s ease 0.05s;
          }
          a:nth-child(5) {
            transition: 0.5s ease 0s;
          }
        }

        .email {
          margin-top: 0;
        }
      }
    }
  }
}

.socials {
  display: flex;
  justify-content: center;
  gap: 20px;
  margin-top: 20px;

  a {
    &:hover,
    &:focus-visible {
      i::before {
        filter: var(--filter-magenta);
      }
    }
    i {
      display: block;
      width: 40px;
      height: 40px;
      border-radius: var(--border-radius);
      background-color: var(--col-primary);
      transition: 0.35s ease;

      &::before {
        content: "";
        display: block;
        width: 100%;
        height: 100%;
        background-size: contain;
        background-position: center;
        background-repeat: no-repeat;
        transition: 0.35s ease;
      }
    }

    &.linked-in {
      i::before {
        background-image: url("../assets/icons8-linkedin.svg");
      }
    }

    &.insta {
      i::before {
        background-image: url("../assets/icons8-instagram.svg");
      }
    }

    &.cults {
      i::before {
        background-image: url("../assets/icons8-delicious.svg");
      }
    }

    &.github {
      i::before {
        background-image: url("../assets/icons8-github.svg");
      }
    }
  }
}
.email {
  margin-top: 20px;
  border: 1px solid #000;
  padding: 5px 15px;
  border-radius: var(--border-radius);
  color: #000;
  background-color: var(--col-primary);
  text-transform: uppercase;
  transition: 0.35s ease;

  &:hover,
  &:focus-visible {
    text-decoration: none;
    border-color: var(--col-magenta);
    color: var(--col-magenta);
  }
}

.main {
  // Hero Section
  .hero {
    position: relative;
    height: 100dvh;
    background-color: var(--col-primary);
    overflow: clip;

    .bg-words {
      position: absolute;
      top: 0;
      left: 15%;
      width: 85%;
      height: 100%;

      div {
        font-weight: 700;
      }
    }
    .nFlex {
      position: relative;
      width: 100%;
      height: 100%;
      display: flex;
      align-items: center;
      justify-content: space-between;

      .empty {
        width: 21%;
      }

      .details {
        display: flex;
        flex-direction: column;
        align-items: center;
        h1 {
          text-align: center;
          font-size: 4rem;
        }

        .sub-title {
          text-align: center;
          font-size: 1.5rem;
        }
      }

      .img-wrapper {
        width: 21%;
        background-color: cyan;
      }
    }
  }
}
